import { reactive } from 'vue';
import { Vue3Marquee } from 'vue3-marquee';
import 'vue3-marquee/dist/style.css';
import { mapMutations, mapState } from 'vuex';
export default {
  name: "musicDetail",
  props: ['musicList', 'playMusic', 'isBtnShow', 'addDuration'],
  components: {
    Vue3Marquee
  },

  setup(props) {
    function showAuthor(authorList) {
      const authors = reactive([]);

      for (let i = 0; i < authorList.length; i++) {
        authors.push(authorList[i].name);
      }

      let author = authors.join('/');
      return author;
    }

    function currentTimeFormat(time) {
      // https://segmentfault.com/a/1190000040388316
      if (!time) return '00:00';
      let interval = Math.floor(time);
      let minute = Math.floor(interval / 60).toString().padStart(2, '0');
      let second = (interval % 60).toString().padStart(2, '0');
      return `${minute}:${second}`;
    }

    return {
      showAuthor,
      currentTimeFormat
    };
  },

  data() {
    return {
      isLyricShow: false
    };
  },

  methods: {
    backHome() {
      this.isLyricShow = false;
      this.updateDetailShow();
    },

    playSong(num) {
      let index = this.playlistIndex + num;

      if (index < 0) {
        index = this.playlist.length;
      } else if (index == this.playlist.length) {
        index = 0;
      }

      this.updatePlaylistIndex(index);
    },

    ...mapMutations(['updateDetailShow', 'updatePlaylistIndex'])
  },
  computed: { ...mapState(['lyricList', 'currentTime', 'playlistIndex', 'playlist', 'duration']),

    lyric() {
      let arr;

      if (this.lyricList.lyric) {
        if (this.lyricList.lyric[0] == '[') {
          // 删除结尾换行
          this.lyricList.lyric = this.lyricList.lyric.substring(0, this.lyricList.lyric.length - 1);
          arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
            let min = item.slice(1, 3);
            let sec = item.slice(4, 6);
            let mill = item.slice(7, item.indexOf(']'));
            let lrc = item.slice(item.indexOf(']') + 1, item.length);
            let time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill); // console.log(min, sec, mill, lrc);

            return {
              min,
              sec,
              mill,
              lrc,
              time
            };
          });
          arr.forEach((item, i) => {
            if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
              item.pre = 100000;
            } else {
              item.pre = arr[i + 1].time;
            }
          });
        } else {
          //没有时间码
          console.log("该歌词现在无法滚动");
          arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/);
        }
      } // console.log(arr);


      return arr;
    }

  },
  watch: {
    currentTime: {
      handler(newValue) {
        if (this.lyricList.lyric[0] == '[') {
          let p = document.querySelector("p.active");

          try {
            if (p.offsetTop > 300) {
              console.log([p][0].innerHTML);
              this.$refs.musicLyric.scrollTop = p.offsetTop - 320;
            }
          } catch {
            console.log("歌词滚动不了");
          }
        }

        if (newValue === this.duration) {
          if (this.playlistIndex == this.playlist.length - 1) {
            this.updatePlaylistIndex(0);
            this.playMusic();
          } else {
            this.updatePlaylistIndex(this.playlistIndex + 1);
          }
        }
      },

      deep: true
    }
  },

  mounted() {
    this.addDuration();
    console.log(this.lyricList.lyric);
  }

};